___CSS_LOADER_EXPORT___.push([module.id, `body.modal-open {
    overflow: hidden;
}

.modal-overlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    z-index: 9999;
    backdrop-filter: blur(5px);
    animation: fadeIn 0.3s ease-out;
}

.modal__form {
    padding: 40px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    max-width: 540px;
    background: white;
    border-radius: 20px;
    box-shadow: 0 25px 60px rgba(0, 0, 0, 0.4);
    z-index: 10000;
    overflow: hidden;
    animation: modalAppear 0.3s ease-out;
}

.modal__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0 32px 0;
    margin-bottom: 0;
}

.modal__title {
    font-size: 1.5rem;
    font-weight: 600;
    color: #1F3F68;
    margin: 0;
    letter-spacing: 1px;
}

.modal__close {
    background: none;
    border: none;
    font-size: 2rem;
    cursor: pointer;
    color: #666;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: all 0.3s ease;
    font-family: Arial, sans-serif;
    padding: 0;
    line-height: 1;
}

.modal__close:hover {
    background: #f5f5f5;
    color: #333;
}

.form-group {
    margin-bottom: 24px;
}

.form-label {
    display: block;
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: 12px;
    color: #1F3F68;
    letter-spacing: 0.5px;
}

.form-input,
.form-textarea {
    width: 100%;
    padding: 16px 18px;
    border: 2px solid #e0e0e0;
    border-radius: 10px;
    font-size: 1rem;
    font-family: inherit;
    transition: all 0.3s ease;
    background: #fafafa;
    box-sizing: border-box;
}

.form-input:focus,
.form-textarea:focus {
    outline: none;
    border-color: #5A98D0;
    background: white;
    box-shadow: 0 0 0 3px rgba(90, 152, 208, 0.1);
}

.form-input::placeholder,
.form-textarea::placeholder {
    color: #999;
    opacity: 1;
}

.form-textarea {
    resize: none;
    min-height: 100px;
}

.submit-button {
    width: 100%;
    padding: 18px;
    background: #5A98D0;
    color: white;
    border: none;
    border-radius: 10px;
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: 4%;
    cursor: pointer;
    transition: all 0.3s ease;
    text-transform: uppercase;
    margin-top: 40px;
    box-sizing: border-box;
}

.submit-button:hover {
    background: #4a87c0;
    transform: translateY(-2px);
    box-shadow: 0 8px 20px rgba(90, 152, 208, 0.4);
}

.submit-button:active {
    transform: translateY(0);
}

.error-message {
    display: block;
    color: #e74c3c;
    font-size: 0.875rem;
    margin-top: 5px;
    min-height: 20px;
}

.form-input.error,
.form-textarea.error {
    border-color: #e74c3c;
    background-color: #fdf2f2;
}

.success-popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  z-index: 10001;
  backdrop-filter: blur(5px);
  animation: fadeIn 0.3s ease-out;
}

.popup-content {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  padding: 40px;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  animation: modalAppear 0.3s ease-out;
  min-width: 300px;
}

.popup-content h3 {
  font-size: 2rem;
  color: #5A98D0;
  margin-bottom: 15px;
  font-weight: 600;
}

.popup-content p {
  font-size: 1.1rem;
  color: #333;
  margin-bottom: 0;
}

.popup-close {
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 2rem;
  cursor: pointer;
  color: #666;
  transition: color 0.3s ease;
}

.popup-close:hover {
  color: #333;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes modalAppear {
    from {
        opacity: 0;
        transform: translate(-50%, -50%) scale(0.8);
    }
    to {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }
}

@media (min-width: 1200px) {
    .modal__form {
        max-width: 580px;
        padding: 45px;
    }
}

@media (min-width: 1400px) {
    .modal__form {
        max-width: 620px;
    }
}

@media (max-width: 768px) {
    .modal__form {
        padding: 35px 25px;
        max-width: 90%;
    }
    
    .modal__header {
        padding: 0 0 25px 0;
    }
    
    .form-group {
        margin-bottom: 20px;
    }
    
    .submit-button {
        margin-top: 35px;
        padding: 16px;
    }
}

@media (max-width: 480px) {
    .modal__form {
        padding: 25px 20px;
        max-width: 95%;
        border-radius: 15px;
    }
    
    .modal__header {
        padding: 0 0 20px 0;
    }
    
    .modal__title {
        font-size: 1.3rem;
    }
    
    .form-group {
        margin-bottom: 18px;
    }
    
    .form-input,
    .form-textarea {
        padding: 14px 16px;
    }
    
    .submit-button {
        margin-top: 30px;
        padding: 14px;
        font-size: 0.9rem;
    }
    
    .form-textarea {
        min-height: 80px;
    }
}

@media (max-width: 360px) {
    .modal__form {
        padding: 20px 15px;
    }
    
    .modal__header {
        padding: 0 0 15px 0;
    }
    
    .modal__title {
        font-size: 1.2rem;
    }
    
    .form-group {
        margin-bottom: 15px;
    }
    
    .submit-button {
        margin-top: 25px;
        padding: 12px;
    }
}
