___CSS_LOADER_EXPORT___.push([module.id, `.footer-section {
    max-width:1366px;
    width: 100%;
    margin: 0 auto;

    border-top-left-radius: 200px;

    background-color: var(--main-color);

    padding: 96px 0 0 0;

    .clients-block {
        display: flex;
        flex-direction: column;
        gap: 111px;

        .about-clients {
            display: flex;
            flex-direction: row;
            gap: 97px;

            max-width: 940px;

            margin: 0 auto;

            .about__title {
                min-width: 230px;
                h3 {
                font-size: 2.25rem;
                line-height: 46px;
                font-weight: 600;
                color: rgba(255, 255, 255, 1);
                letter-spacing: 5%;
                }
            }
            .about__text {
                display: flex;
                align-items: center;
            }
        }

        .clients-grid {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 1px; 

            max-width: 940px;
            width: 100%;

            margin: 0 auto 125px auto;
            background-color: rgba(255, 255, 255, 1);
        }

        .client-item {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 41px 63px;

            transition: all 0.3s ease-in-out;
            background-color: var(--main-color);

            cursor: pointer;
        }

        .client-item:hover {
            background-color: var(--text-dark-blue);
        }
        .client-item:active {
            background-color: var(--main-color);
        }

        .client-logo {
            text-align: center;
            font-weight: 600;
            font-size: 1.25rem;
            line-height: 46px;
            letter-spacing: 2%;
            color: #fff;
        }

        .more-item {
            background-color: #4E87BB;
        }
    }
}

.footer {
    display: flex;
    flex-direction: column;
    background-color: rgba(49, 96, 153, 1);
    
    max-width: 1270px;
    width: 100%;
    border-top-right-radius: 200px;

    .line {
        height: 1px;
        width: 1073px;
        background-color: #fff;
        opacity: 40%;
        margin-bottom: 96px;
    }

    .intersted-block {
        display: flex;
        justify-content: center;
        max-width: 1074px;
        width: 100%;

        margin: 91px auto 96px auto;

        .intersted__text {
            display: flex;
            flex-direction: column;
            gap:32px;
    
            margin-right: 180px;

            h3 {
                display: inline-block;
                max-width: 318px;

                font-size: 2.25rem;
                font-weight: 600;
                line-height: 46px;
                letter-spacing: 5%;
                color: #fff;
            }
            p {
                display: inline-block;
                max-width: 460px;
                opacity: 40%;
                color: #fff;
            }
        }
        .intersted__button {
            .button {
                padding: 15px 75px;
            }
        }
    }
}

.footer__contacts {
    display: flex;
    flex-direction: column;
    gap: 24px;
    list-style: none;
    padding: 0;

    li {
        font-size: 1rem;
        font-weight: 400;
        line-height: 26px;
        letter-spacing: 5%;
        color: #fff;
        max-width: 264px
    }
    .contacts__marker {
            font-weight: 600;
            font-size: 1rem;
            line-height: 26px;
            letter-spacing: 5%;
            color: #fff;
            margin-right: 22px;
    }
}

.container--footer {
    flex-direction: column;
    gap:64px;
}

.footer__information {
    display: flex;
    flex-direction: row;
    gap: 100px;

    max-width: 860px;
    width: 100%;

    .information__left {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    .information__center, .information__right {
        display: flex;
        flex-direction: column;
        gap: 32px;

        span {
            font-size: 1.25rem;
            font-weight: 600;
            line-height: 30px;
            letter-spacing: 5%;
            color:#fff;
        }
        ul {
            display: flex;
            flex-direction: column;
            gap: 16px;

            li {
                font-size: 1rem;
                line-height: 26px;
                font-weight: 400;
                letter-spacing: 5%;
                color: #fff;
                cursor: pointer;
                transition: all 0.2s ease-in-out;
            }
            li:hover {
                opacity: 50%;
            }
            li:active {
                opacity: 30%;
            }
            li:focus {
                opacity: 80%;
            }
        }
    }

    .footer__logo {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 15px;
        cursor: pointer;
        transition: all 0.3s ease-in-out;

        span {
            font-size: 1.75rem;
            font-weight: 600;
            line-height: 46px;
            letter-spacing: 2%;
            color: #fff;
        }
    }
    .footer__logo:hover {
        opacity: 60%;
    }
    .footer__logo:active {
        opacity: 30%;
    }
    .footer__logo:focus {
        opacity: 30%;
    }
}
.footer__copyright {
    max-width: 860px;
    width: 100%;
    text-align: left;
    margin-bottom: 64px;

    .text {
        opacity: 40%;
        color: #fff;
    }
}`, "",{"version":3,"sources":["webpack://./styles/footer-section.scss"],"names":[],"mappings":"AAAA;IACI,gBAAgB;IAChB,WAAW;IACX,cAAc;;IAEd,6BAA6B;;IAE7B,mCAAmC;;IAEnC,mBAAmB;;IAEnB;QACI,aAAa;QACb,sBAAsB;QACtB,UAAU;;QAEV;YACI,aAAa;YACb,mBAAmB;YACnB,SAAS;;YAET,gBAAgB;;YAEhB,cAAc;;YAEd;gBACI,gBAAgB;gBAChB;gBACA,kBAAkB;gBAClB,iBAAiB;gBACjB,gBAAgB;gBAChB,6BAA6B;gBAC7B,kBAAkB;gBAClB;YACJ;YACA;gBACI,aAAa;gBACb,mBAAmB;YACvB;QACJ;;QAEA;YACI,aAAa;YACb,qCAAqC;YACrC,QAAQ;;YAER,gBAAgB;YAChB,WAAW;;YAEX,yBAAyB;YACzB,wCAAwC;QAC5C;;QAEA;YACI,aAAa;YACb,mBAAmB;YACnB,uBAAuB;YACvB,kBAAkB;;YAElB,gCAAgC;YAChC,mCAAmC;;YAEnC,eAAe;QACnB;;QAEA;YACI,uCAAuC;QAC3C;QACA;YACI,mCAAmC;QACvC;;QAEA;YACI,kBAAkB;YAClB,gBAAgB;YAChB,kBAAkB;YAClB,iBAAiB;YACjB,kBAAkB;YAClB,WAAW;QACf;;QAEA;YACI,yBAAyB;QAC7B;IACJ;AACJ;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,sCAAsC;;IAEtC,iBAAiB;IACjB,WAAW;IACX,8BAA8B;;IAE9B;QACI,WAAW;QACX,aAAa;QACb,sBAAsB;QACtB,YAAY;QACZ,mBAAmB;IACvB;;IAEA;QACI,aAAa;QACb,uBAAuB;QACvB,iBAAiB;QACjB,WAAW;;QAEX,2BAA2B;;QAE3B;YACI,aAAa;YACb,sBAAsB;YACtB,QAAQ;;YAER,mBAAmB;;YAEnB;gBACI,qBAAqB;gBACrB,gBAAgB;;gBAEhB,kBAAkB;gBAClB,gBAAgB;gBAChB,iBAAiB;gBACjB,kBAAkB;gBAClB,WAAW;YACf;YACA;gBACI,qBAAqB;gBACrB,gBAAgB;gBAChB,YAAY;gBACZ,WAAW;YACf;QACJ;QACA;YACI;gBACI,kBAAkB;YACtB;QACJ;IACJ;AACJ;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,gBAAgB;IAChB,UAAU;;IAEV;QACI,eAAe;QACf,gBAAgB;QAChB,iBAAiB;QACjB,kBAAkB;QAClB,WAAW;QACX;IACJ;IACA;YACQ,gBAAgB;YAChB,eAAe;YACf,iBAAiB;YACjB,kBAAkB;YAClB,WAAW;YACX,kBAAkB;IAC1B;AACJ;;AAEA;IACI,sBAAsB;IACtB,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,UAAU;;IAEV,gBAAgB;IAChB,WAAW;;IAEX;QACI,aAAa;QACb,sBAAsB;QACtB,SAAS;IACb;;IAEA;QACI,aAAa;QACb,sBAAsB;QACtB,SAAS;;QAET;YACI,kBAAkB;YAClB,gBAAgB;YAChB,iBAAiB;YACjB,kBAAkB;YAClB,UAAU;QACd;QACA;YACI,aAAa;YACb,sBAAsB;YACtB,SAAS;;YAET;gBACI,eAAe;gBACf,iBAAiB;gBACjB,gBAAgB;gBAChB,kBAAkB;gBAClB,WAAW;gBACX,eAAe;gBACf,gCAAgC;YACpC;YACA;gBACI,YAAY;YAChB;YACA;gBACI,YAAY;YAChB;YACA;gBACI,YAAY;YAChB;QACJ;IACJ;;IAEA;QACI,aAAa;QACb,mBAAmB;QACnB,mBAAmB;