___CSS_LOADER_EXPORT___.push([module.id, `.start-section {
    display: flex;
    flex-direction: column;
    align-items: center;

    margin: 0 auto;

    max-width: 100vw;
    width: 100%;
    min-height: 768px;

    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-size: cover;
}
.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    max-width: 1200px;
    width: 100%;
    height: 48px;

    margin: 40px auto 12px auto;

    .logo {
        width: 48px;
        height: 48px;

        cursor: pointer;
    }
    .menu {
        width: 32px;
        height: 32px;

        cursor: pointer;
    }
    .logo, .menu {
        transition: all 0.3s ease-in-out;
    }
    .logo:hover, .menu:hover {
        opacity: 80%;
    }
    .logo:active, .menu:active {
        opacity: 30%;
    }
}

.container--start {
    flex-direction: row;
    justify-content: space-between;
    gap: 5px;
    align-items: center;
    max-width: 1200px;
    width: 100%;

    padding: 0 20px;
}

.content-block {
    display: flex;
    flex: 1;
    flex-direction: column;
    gap:72px;

    .text-container {
        display: flex;
        flex-direction: column;
        gap: 32px;
        
        max-width: 510px;

        .container--text {
            max-width: 350px;
        }
        
        .main-title {
            font-family: 'Times New Roman', Times, serif;
            font-weight: 400;
            font-size: 4rem;
            line-height: 74px;
            letter-spacing: 4%;
            color: var(--text-dark-blue);
        }
    }

    .image-block {
        display: flex;
        flex: 1;
        justify-content: flex-end;
        max-width: 600px;
        
        img {
            max-width: 100%;
            width: auto;
            height: auto;
            object-fit: contain;
        }
    }
}`, "",{"version":3,"sources":["webpack://./styles/start-section.scss"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;;IAEnB,cAAc;;IAEd,gBAAgB;IAChB,WAAW;IACX,iBAAiB;;IAEjB,yDAAoD;IACpD,4BAA4B;IAC5B,sBAAsB;AAC1B;AACA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,mBAAmB;;IAEnB,iBAAiB;IACjB,WAAW;IACX,YAAY;;IAEZ,2BAA2B;;IAE3B;QACI,WAAW;QACX,YAAY;;QAEZ,eAAe;IACnB;IACA;QACI,WAAW;QACX,YAAY;;QAEZ,eAAe;IACnB;IACA;QACI,gCAAgC;IACpC;IACA;QACI,YAAY;IAChB;IACA;QACI,YAAY;IAChB;AACJ;;AAEA;IACI,mBAAmB;IACnB,8BAA8B;IAC9B,QAAQ;IACR,mBAAmB;IACnB,iBAAiB;IACjB,WAAW;;IAEX,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,OAAO;IACP,sBAAsB;IACtB,QAAQ;;IAER;QACI,aAAa;QACb,sBAAsB;QACtB,SAAS;;QAET,gBAAgB;;QAEhB;YACI,gBAAgB;QACpB;;QAEA;YACI,4CAA4C;YAC5C,gBAAgB;YAChB,eAAe;YACf,iBAAiB;YACjB,kBAAkB;YAClB,4BAA4B;QAChC;IACJ;;IAEA;QACI,aAAa;QACb,OAAO;QACP,yBAAyB;QACzB,gBAAgB;;QAEhB;YACI,eAAe;YACf,WAAW;YACX,YAAY;YACZ,mBAAmB;QACvB;IACJ;AACJ","sourcesContent":[".start-section {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n\r\n    margin: 0 auto;\r\n\r\n    max-width: 100vw;\r\n    width: 100%;\r\n    min-height: 768px;\r\n\r\n    background-image: url(../public/images/start-bg.png);\r\n    background-repeat: no-repeat;\r\n    background-size: cover;\r\n}\r\n.header {\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n\r\n    max-width: 1200px;\r\n    width: 100%;\r\n    height: 48px;\r\n\r\n    margin: 40px auto 12px auto;\r\n\r\n    .logo {\r\n        width: 48px;\r\n        height: 48px;\r\n\r\n        cursor: pointer;\r\n    }\r\n    .menu {\r\n        width: 32px;\r\n        height: 32px;\r\n\r\n        cursor: pointer;\r\n    }\r\n    .logo, .menu {\r\n        transition: all 0.3s ease-in-out;\r\n    }\r\n    .logo:hover, .menu:hover {\r\n        opacity: 80%;\r\n    }\r\n    .logo:active, .menu:active {\r\n        opacity: 30%;\r\n    }\r\n}\r\n\r\n.container--start {\r\n    flex-direction: row;